.expense-filter {
    /*display: flex;*/  
    margin: 1rem;
    color: white;
    border-radius: 12px;
    align-items: center;
    justify-content: space-between;
}

.expense-filter__control {
    display: flex;
    justify-content: space-between;
    font-size: 1.3rem;
    font-weight: bold;
}

.expense-filter__control select {
    margin-right: 0;
    width: 9rem;
    height: 2.5rem;
    text-align: center;
    font-size: 1rem;
    font-weight: bold;
    border-radius: 5px;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}